@media all and (max-width: 1200px) {
    .container {
        width: 100%;
    }
    .header-contacts {
        width: 100%;
    }
    .header-catalog {
        position: absolute;
        z-index: 666;
    }
    .wr-down .header-catalog {
        display: none;
    }
    .down-contacts {
        width: 100%;
    }
    .menu-show {
        position: fixed;
        height: 400px;
        top: 50px;
    }
    .wr-why-us {
        div {
            display: flex;
            flex-direction: row;
            justify-content: space-around;
        }
        .why-us__container {
            width: 200px;
            align-items: center;
        }
    }

