@media all and (max-width: 980px) {
    .popular-content {
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 400px 400px 400px;
    }
}
@media all and (max-width: 880px) {
    .element-name,
    .element-have,
    .element-price {
        font-size: 18px;
    }
    .element-have {
        font-size: 18px;
    }
    .element-container {
        height: 380px;
    }
}
