@media all and (max-width: 680px) {
    .logo {
        flex-direction: column;
        align-items: center;
        height: max-content;
        .to-main {
            top: 120px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    .wr-why-us div {
        flex-direction: column;
        align-items: center;
        .why-us__container {
            width: 100%;
        }
    }
}
@media all and (max-width: 650px) {
    .popular-content {
        grid-template-columns: 1fr;
        grid-template-rows: 400px 400px 400px 400px 400px 400px;
    }
    .popular-all {
        padding: 10px 0 40px;
    }
    .wr-why-us .why-us__container {
        padding: 70px 0 20px;
    }
    .wr-gritings {
        h2, p {
            text-align: center;
        }
    }
