@media all and (max-width: 420px) {
    .down-contacts form {
        padding: 0;
    }
    .logo svg {
        padding: 0;
    }
    .down-contacts input, .down-contacts label {
        width: 90%;
    }
    .logo {
        .to-main {
            top: 80px;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}