* {
    &::selection {
        background-color: rgba(0,0,0,0);
    }
    a,p,span {outline: none !important;}
}
body {
    background: rgba(0, 0, 0, 0.1);
}
a {
    text-decoration: none;
    color: black;
}
.container {
    display: block;
    margin: 0 auto;
    width: 1200px;
    height: 100%;
}
.header-menu {
    width: 100%;
    height: 50px;
    background: black;
    font-family: "Open Sans",Helvetica,sans-serif;
    font-weight: 400;
    overflow: hidden;
}
.menu__list {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    li {
        height: 100%;
        line-height: 50px;
    }
    li:last-child {
        position: relative;
        &::before {
            content: '';
            width: 20px;
            height: 20px;
            background: url('../img/call.svg');
            display: block;
            position: absolute;
            top: 15px;
            left: -25px;
        }
    }
    a,
    p {
        color: white;
        &:hover {
            color: gold;
            cursor: pointer;
        }
    }
}
.menu-hover {
    z-index: 10;
    &:hover {
        ul {
            width: 200px;
            height: max-content;
            background: black;
            display: flex;
            justify-content: space-around;
            flex-direction: column;
            li {
                height: 15px;
                line-height: 10px;
                padding: 10px;
            }
        }
    }
    ul {
        position: absolute;
        top: 50px;
        display: block;
        height: 0;
        overflow: hidden;
    }
}

.contacts {
    width: 100%;
    height: 130px;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
}
.logo {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 130px;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    background: white;
    svg {
        height: 80px;
        padding: 20px 30px;
        cursor: pointer;
    }
    image {
        width: 300px;
        height: 80px;
    }
    .to-main {
        position: absolute;
        display: block;
        top: 100px;
        left: 200px;
        width: 100px; 
        height: 20px;
        font-size: 16px;
        opacity: 0.5;
        color: black;
        font-family: "Open Sans",Helvetica,sans-serif;
    }
}
.make-a-call {
    display: block;
    margin: 30px;
    width: 200px;
    height: 20px;
    padding: 20px;
    text-align: center;
    font-family: "Open Sans",Helvetica,sans-serif;
    font-size: 25px;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
    transition: 0.1s ease-in-out;
    &:hover {
        cursor: pointer;
        background: gold;
    }
    &:active {
        transform: scale(0.9);
    }
}
.header-for-catalog {
    position: relative;
    display: flex;
    justify-content: space-between;
    margin: 20px 0;
    width: 100%;
    height: 400px;
}
