.wr-why-us {
    width: 100%;
    box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
    background: black;
    div {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-template-rows: 1fr;
    }
    .why-us__container {
        display: flex;
        flex-direction: column;
        width: 320px;
        color: white;
        text-align: center;
        padding: 70px 20px 20px;
        h2 {
            position: relative;
            font-size: 22px;
            font-family: "Open Sans",Helvetica,sans-serif;
            font-weight: 600;
            padding: 10px 0;
        }
        &:nth-of-type(1) h2::before {
            content: '';
            width: 50px;
            height: 50px;
            position: absolute;
            bottom: 40px;
            left: 50%;
            transform: translateX(-50%);
            background: url("../img/wu-sale.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        &:nth-of-type(3) h2::before {
            content: '';
            width: 50px;
            height: 50px;
            position: absolute;
            bottom: 40px;
            left: 50%;
            transform: translateX(-50%);
            background: url("../img/wu-easy.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        &:nth-of-type(2) h2::before {
            content: '';
            width: 50px;
            height: 50px;
            position: absolute;
            bottom: 40px;
            left: 50%;
            transform: translateX(-50%);
            background: url("../img/wu-hand.png");
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        p {
            font-size: 17px;
            font-family: "Open Sans",Helvetica,sans-serif;
        }
    }
}

