        margin: 0 auto;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 30px;
    }
    input,
    label {
        text-align: center;
        padding: 10px 0;
        font-size: 15px;
        font-family: "Open Sans",Helvetica,sans-serif;
        width: 100%;
        margin: 5px 0;
    }
    input {
        border: 1px solid black;
        box-shadow: 2px 2px 5px 2px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        &::-webkit-inner-spin-button, 
        &::-webkit-outer-spin-button {
            appearance: none;
        }
    }
    .submit {
        background: gold;
        &:hover {
            box-shadow: 2px 2px 5px 2px rgba(255, 217, 0, 0.5);
        }
    }
}
.reserved-list  {
    display: block;
    width: 100%;
    background: white;
    border-radius: 10px;
    h2 {
        text-align: center;
        font-size: 23px;
        font-weight: 600;
        padding: 20px 0;
        font-family: "Open Sans",Helvetica,sans-serif;
    }
    p {
        text-align: center;
        font-size: 18px;
        padding: 8px 0;
        font-family: "Open Sans",Helvetica,sans-serif;
    }
}
.reserved-list {
    display: block;
    width: 320px;
    margin: 0 auto;
    div {
        display: block;
        margin: 0 auto;
        width: 320px;
        height: 250px;
        border-radius: 10px;
        box-shadow: 3px 3px 5px 3px rgba(0, 0, 0, 0.5);
    }
}



