@media all and (max-width: 1200px) {
  .container {
    width: 100%; }
  .header-contacts {
    width: 100%; }
  .header-catalog {
    position: absolute;
    z-index: 666; }
  .wr-down .header-catalog {
    display: none; }
  .down-contacts {
    width: 100%; }
  .menu-show {
    position: fixed;
    height: 400px;
    top: 50px; }
  .wr-why-us div {
    display: flex;
    flex-direction: row;
    justify-content: space-around; }
  .wr-why-us .why-us__container {
    width: 200px;
    align-items: center; } }

@media all and (max-width: 980px) {
  .popular-content {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 400px 400px 400px; } }

@media all and (max-width: 880px) {
  .element-name,
  .element-have,
  .element-price {
    font-size: 18px; }
  .element-have {
    font-size: 18px; }
  .element-container {
    height: 380px; } }

@media all and (max-width: 680px) {
  .logo {
    flex-direction: column;
    align-items: center;
    height: max-content; }
    .logo .to-main {
      top: 120px;
      left: 50%;
      transform: translateX(-50%); }
  .wr-why-us div {
    flex-direction: column;
    align-items: center; }
    .wr-why-us div .why-us__container {
      width: 100%; } }

@media all and (max-width: 650px) {
  .popular-content {
    grid-template-columns: 1fr;
    grid-template-rows: 400px 400px 400px 400px 400px 400px; }
  .popular-all {
    padding: 10px 0 40px; }
  .wr-why-us .why-us__container {
    padding: 70px 0 20px; }
  .wr-gritings h2, .wr-gritings p {
    text-align: center; } }

@media all and (max-width: 420px) {
  .down-contacts form {
    padding: 0; }
  .logo svg {
    padding: 0; }
  .down-contacts input, .down-contacts label {
    width: 90%; }
  .logo .to-main {
    top: 80px;
    left: 50%;
    transform: translateX(-50%); } }
